<template>
    <nav class="navbar is-primary" role="navigation" aria-label="main navigation">
        <div class="navbar-brand">
            <!--<a class="navbar-item" href="/">-->
                <!--<img src="../../assets/shred.svg" width="112" height="90">-->
            <!--</a>-->
            <h2 class="title navbar-item">{{ title }}</h2>
            <a class="button is-dark is-outlined" @click="openCardForm">
                New
            </a>
        </div>
        <card-form :openModal="openForm"></card-form>
    </nav>
</template>

<script>
    import CardForm from '../card/CardForm.vue'
    export default {
        name: 'navbar',
        data: function () {
            return {
                title: 'Shredema',
                openForm: false
            }
        },
        methods: {
            openCardForm: function () {
                this.openForm = !this.openForm;
            }
        },
        components: {
            'card-form': CardForm
        }
    }
</script>

<style scoped>
    .navbar *{
        margin-top: auto;
        margin-bottom: auto;
        /*background-color: red;*/
    }
    .navbar .title {
        margin: auto;
    }
</style>



