import ItemStore from '../stores/ItemStore.js'
const REGRESS_RATE = 1209600000; // 2 weeks
const ItemService = {
    create(item) {
        item = prepareItem(item);

        // http call..
    },
    update() {
        // http call..
    },
    remove() {
        // http call..
    },
    getList() {
        // http call..
        let data = JSON.parse(localStorage.getItem('state'));
        if (data) {
            data.list.forEach((item) => {
                item.regress = this.calculateRegress(item);
            })
        }
        return data;
    },
    getItemIndex (item) { // TODO: make 'storeless'
        let index;
        for (let i = 0; i < ItemStore.state.list.length;  i++) {
            if(ItemStore.state.list[i].id === item.id) {
                index = i;
                break;
            }
        }
        return index;
    },
    calculateRegress(item) {
        let diff = Date.now() - new Date(item.rehearsed_at).getTime()
        let percent =  100 - diff * 100 / REGRESS_RATE;
        if (percent < 0) {
            percent = 0;
        }
        return percent;
    }
}

function prepareItem(item) {
    item.id = getNextId();
    item.created_at = new Date();
    item.rehearsed_at = new Date();
    item.regress = ItemService.calculateRegress(item);
}

function getNextId () { // TODO: make 'storeless'
    ItemStore.state.nextId++
    return ItemStore.state.nextId
}

export default ItemService


