<template>
    <div class="progress-control">
        <div class="field">
            <div class="tile is-ancestor">
                <div class="tile is-2">
                    <div class="control">
                        <label class="checkbox">
                            <input type="checkbox" v-model="rhythmSelection" :disabled="defaultSelection">
                            Rhythm
                        </label>
                    </div>
                </div>
                <div class="tile">
                    <div class="control">
                        <slider :disabled="!rhythmSelection" v-bind:class="rhythmSelection || defaultSelection? 'is-success' : 'is-light'" size="medium" :value="rhythmValue" :max="100" :step="1" is-fullwidth @change="updateRhythmValue"></slider>
                    </div>
                </div>
            </div>
        </div>
        <div class="field">
            <div class="tile is-ancestor">
                <div class="tile is-2">
                    <div class="control">
                        <label class="checkbox">
                            <input type="checkbox" v-model="soloSelection">
                            Solo
                        </label>
                    </div>
                </div>
                <div class="tile">
                    <div class="control">
                        <slider :disabled="!soloSelection" v-bind:class="soloSelection ? 'is-success' : 'is-light'" size="medium" :value="soloValue" :max="100" :step="1" is-fullwidth @change="updateSoloValue"></slider>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Slider from 'vue-bulma-slider'
    export default {
        name: 'progress-control',
        props: ['rhythm', 'solo', 'submitted'],
        data: function () {
            return {
                rhythmSelection: false,
                soloSelection: false,
                rhythmValue: 100,
                soloValue: 100
            }
        },
        watch: {
            submitted: function(newVal) {
                if (newVal) {
                    this.returnData();
                }
            },
            rhythm: function(data) {
                this.rhythmSelection = data.state;
                this.rhythmValue = data.value;
            },
            solo: function(data) {
                this.soloSelection = data.state;
                this.soloValue = data.value;
            }
        },
        computed: {
            'defaultSelection': function () {
                let isNoneSelected = !this.soloSelection && !this.rhythmSelection;
                if (isNoneSelected) {
                    this.rhythmSelection = !this.soloSelection && !this.rhythmSelection
                }
                return isNoneSelected;
            }
        },
        methods: {
            'updateSoloValue': function (val) {
                this.soloValue = Number(val)
            },
            'updateRhythmValue': function (val) {
                this.rhythmValue = Number(val)
            },
            'returnData': function () {
                let data = {
                    rhythm: {
                        value: this.rhythmValue,
                        state: this.rhythmSelection
                    },
                    solo: {
                        value: this.soloValue,
                        state: this.soloSelection
                    }
                }
                this.$emit('returnData', data)
            }
        },
        components: {
            'slider': Slider
        }
    }
</script>

<style scoped>
    .checkbox {
        color: initial;
        padding-bottom: 5px;
    }
    .checkbox input {
        top: 2px;
        position: relative;
    }
    .tile .control {
        width: 100%;
        margin: auto;
    }
    .field:first-child {
        margin-bottom: 25px;
    }
</style>



