<template>
    <div class="card">
        <div class="card-content">
            <p class="title">
                {{ card.title }}
            </p>
            <p class="subtitle">
                {{ card.artist }}
            </p>
            <progress v-bind:class="[{ 'is-success': card.regress > 65, 'is-warning': card.regress <= 65 && card.regress > 35, 'is-danger': card.regress <= 35}]"
                      class="progress" :value="card.regress" max="100"></progress>
            <p class="subtitle">
                {{ progressPercentage }} %
            </p>
        </div>
        <footer class="card-footer">
            <a @click="editCard" class="card-footer-item">Edit</a>
            <a @click="deleteCard(card)" class="card-footer-item">Delete</a>
            <a @click="resetRegress(card)" class="card-footer-item">Power Up!</a>
        </footer>
        <card-form :openModal="openForm" :card="card" :edit="true"></card-form>
    </div>
</template>

<script>
    import CardForm from './CardForm.vue'
    import ItemStore from '../../stores/ItemStore.js'
    export default {
        name: 'card',
        props: {
            card: {}
        },
        data: function () {
            return {
                openForm: false
            }
        },
        computed: {
            progressPercentage:  function () {
                let soloVal = this.card.solo.state ? this.card.solo.value : 0;
                let rythmVal = this.card.rhythm.state ? this.card.rhythm.value : 0;
                let maxValue = this.card.solo.state && this.card.rhythm.state ? 200 : 100;
                return (soloVal + rythmVal) * 100 / maxValue;
            }
        },
        methods: {
            'deleteCard': function (card) {
                ItemStore.removeItem(card)
            },
            'editCard': function () {
                this.openForm = !this.openForm;
            },
            'resetRegress': function (card) {
                this.card.rehearsed_at = Date.now();
                this.smoothProgressAnimation(card);
            },
            'smoothProgressAnimation': function (card) {
                let loop = setInterval(function () {
                    if (card.regress < 100) {
                        card.regress += 1;
                        if (card.regress > 100) {
                            card.regress = 100;
                        }
                    } else {
                        clearInterval(loop);
                    }
                }, 15);
            }
        },
        components: {
            'cardForm': CardForm
        }
    }
</script>

<style scoped>
    .card {
        display: inline-block;
        width: 300px;
        margin: 10px;
    }
</style>



