<template>
    <div class="card-form">
        <div class="modal">
            <div class="modal-background"></div>
            <div class="modal-card">
                    <header class="modal-card-head">
                        <p class="modal-card-title">{{ edit ? 'Edit' : 'New' }} Song</p>
                    <button class="delete" aria-label="close" @click="closeModal"></button>
                </header>
                <section class="modal-card-body">
                    <form>
                        <div class="field">
                            <label class="label is-pulled-left">Artist</label>
                            <div class="control">
                                <input v-bind:class="{ 'is-danger': error }" class="input" v-model="item.artist" type="text" placeholder="e.g The Demos" @input="resetError">
                            </div>
                            <label class="label is-pulled-left">Title</label>
                            <div class="control">
                                <input v-bind:class="{ 'is-danger': error }" class="input" v-model="item.title" type="text" placeholder="e.g Rock it" @input="resetError">
                            </div>
                        </div>
                        <p v-if="error" class="help is-danger">
                            All fields are required
                        </p>
                        <div class="box">
                            <progress-control v-on:returnData="handleProgressChange" :submitted="submitted" :rhythm="item.rhythm" :solo="item.solo"></progress-control>
                        </div>
                    </form>
                </section>
                <footer class="modal-card-foot">
                    <button class="button is-success" @click="submit(item)">Save</button>
                    <button class="button" @click="closeModal">Cancel</button>
                </footer>
            </div>
        </div>
    </div>
</template>

<script>
    import ItemStore from '../../stores/ItemStore.js'
    import ProgressControl from './form/ProgressControl.vue'

    export default {
        name: 'card-form',
        data: function () {
            return {
                defaultItem: {
                    title: '',
                    artist: '',
                    regress: 0,
                    rhythm: {
                        value: 100,
                        state: true
                    },
                    solo: {
                        value: 100,
                        state: false
                    }
                },
                item: {},
                submitted: false,
                error: false
            }
        },
        props: {
            openModal: Boolean,
            card: Object,
            edit: Boolean
        },
        watch: {
            'openModal': function () {
                this.submitted = false;
                this.error = false;
                if (this.card && this.edit) {
                    this.item = this.card;
                } else {
                    this.item = JSON.parse(JSON.stringify(this.defaultItem));
                }

                let context = this;
                let modal = this.$el.querySelector('.modal')
                modal.classList.toggle('is-active')
                modal.querySelector('.modal-background').onclick = function () {
                    context.closeModal()
                }
            }
        },
        methods: {
            closeModal: function () {
                let modal = this.$el.querySelector('.modal')
                modal.classList.toggle('is-active')
                this.item = JSON.parse(JSON.stringify(this.defaultItem));
            },
            submit: function (item) {
                console.log('submit: ', JSON.parse(JSON.stringify(item)));
                if (this.isValid(item)) {
                    this.submitted = true;
                } else {
                    this.error = true;
                }
            },
            submitData: function (item) {
                if (this.edit) {
                    ItemStore.updateItem(item)
                } else {
                    ItemStore.addItem(item)
                }
                this.closeModal()
            },
            isValid: function (item) {
                return item.title.trim() && item.artist.trim()
            },
            handleProgressChange: function(data) {
                this.item.rhythm = data.rhythm;
                this.item.solo = data.solo;
                this.submitData(this.item)
            },
            resetError: function() {
                this.error = false;
            }
        },
        components: {
            'progressControl': ProgressControl
        }
    }
</script>

<style scoped>

</style>



